export async function createScene(engine, canvas, BABYLON) {
  const scene = new BABYLON.Scene(engine);
  console.clear();
  scene.clearColor = new BABYLON.Color4(0.75, 0.75, 0.9);

  const camera = new BABYLON.ArcRotateCamera("camera", -0.873, 1, 14, BABYLON.Vector3.Zero(), scene);
  camera.attachControl(canvas, true);

  const light = new BABYLON.HemisphericLight("light", new BABYLON.Vector3(0, 1, 0), scene);

  var advancedTexture = BABYLON.GUI.AdvancedDynamicTexture.CreateFullscreenUI("UI");
  advancedTexture.idealWidth = 600;

  const stepCount = 5;
  const stepHeight = 0.5;
  const stepDepth = 1;
  const stepWidth = 3;

  const steps = new BABYLON.TransformNode("Step", scene);
  steps.position.y = 0.2;
  steps.position.z = -3.5;

  for (let i = 0; i < stepCount; i++) {
    const step = BABYLON.MeshBuilder.CreateBox(
      "step" + i,
      {
        width: stepWidth,
        height: stepHeight,
        depth: stepDepth,
      },
      scene
    );
    step.position.set(0, i * stepHeight, i * stepDepth);
    step.parent = steps;
  }

  const ground = BABYLON.MeshBuilder.CreateBox(
    "ground",
    {
      width: 8,
      height: 0.1,
      depth: 15,
    },
    scene
  );
  ground.position.y = -0.05;

  const indicator = new BABYLON.TransformNode("Indicator", scene);
  function createArrow(scene) {
    var indicatorMat = new BABYLON.StandardMaterial("indicatorMat", scene);
    indicatorMat.diffuseColor = new BABYLON.Color3(1, 0.1, 0.25);

    var arrowBody = BABYLON.MeshBuilder.CreateCylinder("arrowBody", { height: 0.3, diameter: 0.1 }, scene);
    var arrowHead = BABYLON.MeshBuilder.CreateCylinder("arrowHead", { height: 0.15, diameterTop: 0, diameterBottom: 0.2 }, scene);
    arrowHead.position.y = 0.15;

    arrowBody.material = indicatorMat;
    arrowHead.material = indicatorMat;

    arrowBody.setParent(indicator);
    arrowHead.setParent(indicator);

    indicator.position = steps.getChildMeshes()[4].absolutePosition.clone().add(new BABYLON.Vector3(0.2, 0.7, -0.2));
    indicator.rotationQuaternion = BABYLON.Quaternion.RotationAxis(BABYLON.Axis.X, Math.PI);

    var rect = new BABYLON.GUI.Rectangle("Indicator Rect");
    rect.width = 0.2;
    rect.height = "30px";
    rect.width = "300px";
    rect.cornerRadius = 12;
    rect.color = "black";
    rect.thickness = 0;
    rect.background = "transparent";
    advancedTexture.addControl(rect);
    rect.linkWithMesh(indicator);
    rect.linkOffsetY = -40;

    var label = new BABYLON.GUI.TextBlock("Indicator label");
    label.text = "Click Here!";
    label.resizeToFit = true;
    rect.addControl(label);

    animateIndicator();
  }
  createArrow();

  var arrowMat = new BABYLON.StandardMaterial("arrowMat", scene);
  arrowMat.diffuseColor = new BABYLON.Color3(0.1, 0.2, 1);

  var arrowMeshes = [];

  function createChevron(scene) {
    var chevronShape = [
      new BABYLON.Vector3(-0.2, 0, -0.1),
      new BABYLON.Vector3(0, 0, 0.1),
      new BABYLON.Vector3(0.2, 0, -0.1),
      new BABYLON.Vector3(0, 0, -0.05),
    ];
    var chevronMesh = BABYLON.MeshBuilder.ExtrudePolygon("chevron", { shape: chevronShape, depth: 0.02 }, scene);
    chevronMesh.material = arrowMat;
    chevronMesh.renderingGroupId = 1;
    return chevronMesh;
  }

  function placeArrows(pathPoints) {
    arrowMeshes.forEach((mesh) => mesh.dispose());
    arrowMeshes = [];

    let arrowSpacing = 0.4;

    for (let i = 1; i < pathPoints.length; i++) {
      let prev = pathPoints[i - 1];
      let current = pathPoints[i];
      let segmentLength = BABYLON.Vector3.Distance(prev, current);
      let direction = current.subtract(prev).normalize();
      let stepCount = Math.floor(segmentLength / arrowSpacing);

      for (let j = 0; j < stepCount; j++) {
        let t = (j + 1) / (stepCount + 1);
        let position = BABYLON.Vector3.Lerp(prev, current, t);
        let chevron = createChevron(scene);
        chevron.position = position;
        let angle = Math.atan2(direction.x, direction.z);
        chevron.rotation.y = angle;
        arrowMeshes.push(chevron);
      }
    }

    animateArrows();
  }

  function animateIndicator() {
    let animation = new BABYLON.Animation(
      "indicator move",
      "position",
      10,
      BABYLON.Animation.ANIMATIONTYPE_VECTOR3,
      BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE
    );

    let keys = [
      { frame: 0, value: indicator.position },
      { frame: 10, value: indicator.position.add(new BABYLON.Vector3(0, 0.5, 0)) },
      { frame: 20, value: indicator.position },
    ];
    animation.setKeys(keys);

    scene.beginDirectAnimation(indicator, [animation], 0, 20, true);
  }

  function animateArrows() {
    arrowMeshes.forEach((arrow, index) => {
      let animation = new BABYLON.Animation(
        "arrowPulse",
        "visibility",
        10,
        BABYLON.Animation.ANIMATIONTYPE_FLOAT,
        BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE
      );

      let keys = [
        { frame: 0, value: 0.15 },
        { frame: 10, value: 1.0 },
        { frame: 20, value: 0.15 },
      ];
      animation.setKeys(keys);

      let delay = index * 2;
      scene.beginDirectAnimation(arrow, [animation], delay, 20, true);
    });
  }

  let navigationPlugin = new BABYLON.RecastJSPlugin();
  var navmeshParameters = {
    cs: 0.2,
    ch: 0.2,
    walkableSlopeAngle: 90,
    walkableHeight: 2,
    walkableClimb: 3,
    walkableRadius: 0.3,
    maxEdgeLen: 0,
    maxSimplificationError: 3.5,
    minRegionArea: 1,
    mergeRegionArea: 15,
    maxVertsPerPoly: 6,
    detailSampleDist: 1,
    detailSampleMaxError: 0.5,
  };

  navigationPlugin.createNavMesh(
    scene.meshes.filter((mesh) => !mesh.isDescendantOf(indicator)),
    navmeshParameters
  );
  var navmeshdebug = navigationPlugin.createDebugNavMesh(scene);
  navmeshdebug.position = new BABYLON.Vector3(0, 0.01, 0);

  var matdebug = new BABYLON.StandardMaterial("matdebug", scene);
  matdebug.diffuseColor = new BABYLON.Color3(0.1, 0.2, 1);
  matdebug.alpha = 0.2;
  navmeshdebug.material = matdebug;

  var agents = [];
  var startingPoint;
  var hasArrived = false;

  var crowd = navigationPlugin.createCrowd(10, 0.1, scene);
  var i;
  var agentParams = {
    radius: 0.3,
    height: 0.4,
    maxAcceleration: 8.0,
    maxSpeed: 3.0,
    collisionQueryRange: 0.5,
    pathOptimizationRange: 0.0,
    separationWeight: 1.0,
  };

  for (i = 0; i < 1; i++) {
    var width = 0.2;
    var agentCube = BABYLON.MeshBuilder.CreateBox("cube", { size: width, height: width }, scene);
    var targetCube = BABYLON.MeshBuilder.CreateBox("cube", { size: 0.1, height: 0.1 }, scene);
    var matAgent = new BABYLON.StandardMaterial("mat2", scene);
    var variation = Math.random();
    matAgent.diffuseColor = new BABYLON.Color3(0.4 + variation * 0.6, 0.3, 1.0 - variation * 0.3);
    agentCube.material = matAgent;
    var randomPos = navigationPlugin.getRandomPointAround(new BABYLON.Vector3(-2.0, 0.1, -1.8), 0.5);
    var transform = new BABYLON.TransformNode();
    var agentIndex = crowd.addAgent(randomPos, agentParams, transform);
    agents.push({ idx: agentIndex, trf: transform, mesh: agentCube, target: targetCube });

    var rect1 = new BABYLON.GUI.Rectangle("Agent Rect");
    rect1.width = 0.2;
    rect1.height = "20px";
    rect1.width = "60px";
    rect1.cornerRadius = 12;
    rect1.color = "Orange";
    rect1.thickness = 4;
    rect1.background = "green";
    advancedTexture.addControl(rect1);
    rect1.linkWithMesh(agentCube);
    rect1.linkOffsetY = -60;

    var label = new BABYLON.GUI.TextBlock("Agent TB");
    label.text = "Agent";
    label.resizeToFit = true;
    rect1.addControl(label);

    var target = new BABYLON.GUI.Ellipse("Agent El");
    target.width = "4px";
    target.height = "4px";
    target.color = "Orange";
    target.thickness = 2;
    target.background = "green";
    advancedTexture.addControl(target);
    target.linkWithMesh(agentCube);

    var line = new BABYLON.GUI.Line("Agent Line");
    line.lineWidth = 2;
    line.color = "Orange";
    line.y2 = 8;
    line.linkOffsetY = -1;
    advancedTexture.addControl(line);
    line.linkWithMesh(agentCube);
    line.connectedControl = rect1;
  }

  var getGroundPosition = function () {
    var pickinfo = scene.pick(scene.pointerX, scene.pointerY);
    if (pickinfo.hit) {
      return pickinfo.pickedPoint;
    }

    return null;
  };

  var lineMat = new BABYLON.StandardMaterial("line", scene);
  lineMat.emissiveColor = new BABYLON.Color3(0.35, 0.42, 1);

  var pointerDown = function (mesh) {
    startingPoint = getGroundPosition();
    if (startingPoint) {
      setTimeout(function () {
        camera.detachControl(canvas);
      }, 0);
      var agents = crowd.getAgents();
      var i;
      for (i = 0; i < agents.length; i++) {
        crowd.agentGoto(agents[i], navigationPlugin.getClosestPoint(startingPoint));
      }
      var pathPoints = navigationPlugin.computePath(crowd.getAgentPosition(agents[0]), navigationPlugin.getClosestPoint(startingPoint));

      if (pathPoints.length > 1) {
        placeArrows(pathPoints);
      }
    }
  };

  scene.onPointerObservable.add((pointerInfo) => {
    switch (pointerInfo.type) {
      case BABYLON.PointerEventTypes.POINTERDOWN:
        if (pointerInfo.pickInfo.hit) {
          pointerDown(pointerInfo.pickInfo.pickedMesh);
        }
        break;

      case BABYLON.PointerEventTypes.POINTERUP:
        if (pointerInfo.pickInfo.hit) {
          indicator.dispose();

          var indicatorRect = advancedTexture.getDescendants().find((control) => control.name === "Indicator Rect");

          if (indicatorRect) indicatorRect.dispose();
        }
        break;
      default:
        break;
    }
  });

  scene.onBeforeRenderObservable.add(() => {
    var agentCount = agents.length;
    for (let i = 0; i < agentCount; i++) {
      var ag = agents[i];
      ag.mesh.position = crowd.getAgentPosition(ag.idx);
      let vel = crowd.getAgentVelocity(ag.idx);
      crowd.getAgentNextTargetPathToRef(ag.idx, ag.target.position);

      if (vel.length() > 0.2) {
        vel.normalize();
        var desiredRotation = Math.atan2(vel.x, vel.z);
        ag.mesh.rotation.y = ag.mesh.rotation.y + (desiredRotation - ag.mesh.rotation.y) * 0.05;
      }

      if (navigationPlugin && startingPoint instanceof BABYLON.Vector3) {
        let targetPosition = navigationPlugin.getClosestPoint(startingPoint);
        if (targetPosition) {
          checkAgentArrival(ag, targetPosition);
        }
      }
    }
  });

  function checkAgentArrival(agentIndex, targetPosition) {
    let agentPosition = crowd.getAgentPosition(agentIndex);
    let distance = BABYLON.Vector3.Distance(agentPosition, targetPosition);
    if (distance < 0.1) {
      // console.log("Agent has reached the destination!");
      arrowMeshes.forEach((mesh) => mesh.dispose());
      arrowMeshes = [];

      let referencePosition = steps.getChildMeshes()[4].absolutePosition;
      if (compareDist(agentPosition, referencePosition)) {
        if (!hasArrived) {
          alert("ðŸŽ‰ Well Done ðŸŽ‰");
          hasArrived = true;
        }
      }
    }
  }

  function compareDist(targetPosition, referencePosition, threshold = 1) {
    return BABYLON.Vector3.Distance(targetPosition, referencePosition) <= threshold;
  }

  return scene;
}
